.basket-item{
	 display: flex;
	 justify-content: space-between;
	 flex-wrap: wrap;
	 padding-right: 30px;
	 border-bottom: 1px solid #E8ECEC;
	 &:last-child{
		 border-bottom: 0;
	 }
	 .for--mob{
		 display: none;
	 }
	 &__img{
		 height: 100%;
		 width: 18%;
		 padding: 20px 30px;
		 .bg{
			 width: 100%;
			 height: 100%;
			 min-height: 100px;
			 background-size: contain;
			 background-repeat: no-repeat;
			 background-position: center;
		 }
	 }
	 &__name{
		 padding-top: 30px;
		 font-weight: 600;
		 font-size: 15px;
		 line-height: 135%;
		 width: 22%;
		 color: #4F5050;
	 }
	 &__discount,
	 &__price,
	 &__weight,
	 &__count,
	 &__total{
		 padding-top: 30px;
		 font-size: 14px;
		 line-height: 135%;
		 color: #4F5050;
		 text-align: center;
		 width: 12%;
	 }
	 &__discount{
		 font-weight: 600;
		 font-size: 14px;
		 line-height: 135%;
		 text-align: center;
		 color: #F74B4B;
	 }
	 &__price{
		 text-align: center;
		 .old{
			 font-size: 13px;
			 line-height: 135%;
			 text-decoration-line: line-through;
			 color: #A7A7A7;
			 display: block;
		 }
		 .new{
			 font-weight: 600;
			 font-size: 14px;
			 line-height: 135%;
			 color: #4F5050;
			 display: block;
		 }
	 }
	 &__total{
		 .old{
			 font-size: 13px;
			 line-height: 135%;
			 display: block;
			 /* identical to box height, or 18px */

			 text-decoration-line: line-through;

			 color: #A7A7A7;

		 }
		 .new{
			 display: block;
			 font-weight: 600;
			 font-size: 14px;
			 line-height: 135%;
			 /* identical to box height, or 19px */


			 color: #F74B4B;
		 }
	 }
 }

.basket-item-page{
	padding-right: 100px;
	position: relative;
}
.basket-items-list{
	.basket-container__param ul{
		padding-right: 100px;
	}
}

.basket-item__delet{
	width: 90px;
	height: 100%;
	position: absolute;
	right: 0;
	top: 0;
	display: flex;
	align-items: flex-start;
	padding-top: 30px;
	justify-content: center;
	.delete{
		width: 30px;
		height: 30px;
		border: 0;
		background: none;
		cursor: pointer;
		svg{
			fill: #454545;
			transition: .3s;
		}
		&:hover{
			svg {
				fill: #a2a2a2;
			}
		}
	}
}

.click-counter{
	display: flex;
	align-items: center;
	justify-content: center;
	button{
		border: 0;
		background: none;
		font-size: 20px;
		font-weight: 100;
		padding: 0 5px;
		color: #4F5050;
		font-weight: lighter;
	}
	.plus{

	}
	input{
		width: 40px;
		border: 0;
		font-size: 14px;
		line-height: 135%;
		text-align: center;
		color: #4F5050;
	}
	.minus{

	}
}