@import 'basket-item', 'basket-item-mini', 'basket-header';

.basket-container{
	background: #FFFFFF;
	border: 1px solid #CFD5D5;
	border-radius: 4px;
	margin-bottom: 40px;
	&__top{
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 18px 30px;
		border-bottom: 1px solid #cfd5d5;
		*{
			line-height: 135%;
		}
		span{
			font-weight: 600;
			font-size: 18px;
			color: #1F1F1F;
		}
		a{
			font-size: 14px;
			text-decoration-line: underline;
			color: #F74B4B;
			width: 12%;
			text-align: center;
			&:hover{
				text-decoration: none;
			}
		}
	}
	&__param{
		padding: 12px 0;
		border-bottom: 1px solid #CFD5D5;
		ul{
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			padding-right: 30px;
			li{
				font-size: 14px;
				text-align: center;
				line-height: 135%;
				color: #919393;
				width: 12%;
				&:nth-child(1){
					width: 40%;
					padding-left: 30px;
					text-align: left;
				}

			}
		}
	}
}